*{box-sizing: border-box;}

html,body{
height: 100%;
display: flex;
justify-content: center;
}
body{
margin:0;
padding: 0;
background-color: #eff3f7;
}
.container{
height: 100%;
display: flex;
justify-content:center;
}
.loader{
width:100%;
height: 100%;
display: flex;
justify-content: center;
align-items: center;
}

.movies {
padding-top: 70px;
width: 80%;
display: flex;
justify-content: space-between;
align-items: flex-start;
flex-wrap: wrap;
padding: 50px;
}